let initialState = {
  times: [{id:0},{id:1},{id:2},{id:3},{id:4},{id:5},{id:6},{id:7},{id:8},{id:9},{id:10},{id:11},{id:12},{id:13},{id:14},{id:15}],
  instrument: 'kick',
  active: false
}
const reducer = (state=initialState, { type, payload }) => {
  switch (type) {
    case 'CHANGE_INSTRUMENT': 
      return {
        ...state,
        instrument: payload
      }
    case 'ADD_TO_TIME':
      const newTimesAdd = [...state.times]
      newTimesAdd[payload.id] = {
        ...newTimesAdd[payload.id],
        [payload.instrument]:true
      }
      return {
        ...state,
        times: newTimesAdd
      }
    case 'REMOVE_TO_TIME':
      const newTimesRemove = [...state.times]
      newTimesRemove[payload.id] = {
        ...newTimesRemove[payload.id],
        [payload.instrument]:false
      }
      return {
        ...state,
        times: newTimesRemove
      }
    case 'SET_ACTIVE':
      return {
        ...state,
        active: payload
      }
    default: return state
  }
}
export default reducer
