import React, { useState } from 'react'
import s from './Square.module.css'
import { useDispatch, useSelector } from 'react-redux'
import { addToTime, removeToTime } from '../redux/actions'

const Square = ({id, time}) => {
  const [clicked, setClicked] = useState({})
  const instrument = useSelector(state => state.instrument)
  const dispatch = useDispatch()

  const handleClick = e => {
    if(e.target.className !== s.active) { //Gives the styles to the squares
      !clicked[instrument]
        ? e.target.className = s[instrument]
        : e.target.className = s.square
    }

    if(!clicked[instrument]) { //Adds/removes the instruments to the store
      dispatch(addToTime({id, instrument}))
    } else {
      dispatch(removeToTime({id, instrument}))
    }
    setClicked({
      ...clicked,
      [instrument] : !clicked[instrument]
    })
  }
  return (
      <div 
        className={
          time === id 
            ? s.active 
            : clicked[instrument] 
              ? s[instrument] 
              : s.square
        }
        onClick={handleClick} 
      >
    </div>
  )
}

export default Square
