import { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { changeInstrument, setActive } from '../redux/actions'
import { setTracker, playInstruments } from './utils'
import Square from './Square'
import s from './Squares.module.css'

const lang = window.navigator.language || navigator.browserLanguage;

const Squares = () => {
  const [timeTrack, setTimeTrack] = useState(0)
  const [pulse, setPulse] = useState(90)
  const { active, times } = useSelector(state => state)
  const dispatch = useDispatch()

  useEffect( ()=> {
    const interval = setTracker(active, setTimeTrack, timeTrack, times, pulse)

    playInstruments(active, times[timeTrack])

    return () => {
      clearInterval(interval)}
  }, 
  [timeTrack, active, pulse])

  const handleStart = () => {
    setTimeTrack(0)
    dispatch(setActive(!active))
  }
  const handlePulseChange = e => {
    setPulse(e.target.value)
  }
  const handleInstrumentChange = e => {
    dispatch(changeInstrument(e.target.value))
  }

  return (
    <main>
      <section className={s.squares_box}>
        {times.map(el => {
          return (
            <Square time={timeTrack} id={el.id} key={el.id} />
          )
        })
        }
      </section>
      <section className={s.handlers}>
        <button className={s.start_btn} onClick={handleStart}>
          {
            lang.includes('es') 
            ? (!active ? 'Empezar' : 'Pausar')
            : (!active ? 'Play' : 'Pause')
          }
        </button>
        <div className={s.pulse_box}>
          <label className={s.pulse_label}  htmlFor="pulse">{lang.includes('es') ? 'Pulso' : 'Pulse'}</label>
          <input className={s.pulse_handler} value={pulse} name="pulse" onChange={handlePulseChange} min="40" max="180" type='range'/>
          <span>{pulse}</span>
        </div>
        <select className={s.instrument_select} onChange={handleInstrumentChange} name="instrument">
          <option value="kick" defaultValue>{lang.includes('es') ? 'Bombo' : 'Kick'}</option>
          <option value="snare">{lang.includes('es') ? 'Caja' : 'Snare'}</option>
          <option value="hat">{lang.includes('es') ? 'Hi Hat' : 'Hat'}</option>
          <option value="clap">{lang.includes('es') ? 'Palmas' : 'Clap'}</option>
          <option value="crash">{lang.includes('es') ? 'Platillo' : 'Crash'}</option>
          <option value="perc">Perc</option>
        </select>
      </section>
    </main>
  )
}

export default Squares
