import samples from '../media/samples' 

export const setTracker = (active, setTimeTrack, timeTrack, times, pulse) => {
  let interval
  if(active) {
    if(timeTrack >= times.length-1) {
      interval = setInterval(() => {
        setTimeTrack(0)
      }, 500 / (pulse/60) )
    } else {
      interval = setInterval(() => {
        setTimeTrack(timeTrack + 1)
      }, 500 / (pulse/60) )
    }
  }
  return interval
}

export const playInstruments = (active, actual) => {
  const { kick, snare, hat, clap, crash, perc } = samples
  if(active && actual) {
    actual.kick && kick.play()
    actual.snare && snare.play()
    actual.hat && hat.play()
    actual.crash && crash.play()
    actual.clap && clap.play()
    actual.perc && perc.play()
  }
}
