export const changeInstrument = payload => {
  return{
    type: 'CHANGE_INSTRUMENT', 
    payload
  }
}

export const addToTime = payload => {
  return {
    type: 'ADD_TO_TIME',
    payload
  }
}

export const removeToTime = payload => {
  return {
    type: 'REMOVE_TO_TIME',
    payload
  }
}

export const setActive = payload => {
  return {
    type: 'SET_ACTIVE', 
    payload
  }
}
